import axios from './http'
export default {
    /**
     * 分页获取文章
     * @param {*} params 
     * @param {*} page 第几页
     * @param {*} limit 每页数量
     */
    getArticlePage(params) {
        return axios.get("/api/article", {
            params
        })
    },
    /**
    * 根据id获取文章
    * @param {*} params id
    * @param {*} id 文章id
    */
    getArticleById(id) {
        return axios.get(`/api/article/${id}`)
    },
    /**
     * 根据用户id获取文章
     * @param {*} params  object
     * @param {*} userId  
     */
    getArticleByUser(params) {
        return axios.get("/api/article", {
            params
        })
    },
    // 获取文章，阅读，评论数量
    getArticleMes() {
        return axios.get('/api/other/article')
    },
    //增加浏览量
    addArticleVivew(articleId) {
        return axios.put(`/api/article/${articleId}`);
    },
    // 根据用户id获取用户信息
    getUserById(id) {
        return axios.get(`/api/user/${id}`)
    },
    // 根据文章id获取点赞数
    getPraiseByArticleId(params) {
        return axios.get(`/api/praise`, {
            params
        })
    },
    // 根据文章id获取评论
    getCommentByArticleId(articleId) {
        return axios.get(`/api/comment/${articleId}`)
    },
    // 提交评论
    submitComment(params) {
        return axios.post('/api/comment', {
            data: params
        })
    },
    // 登录
    login(params) {
        return axios.post('/api/user/login', {
            data: params
        })
    },
    // 注册
    register(params) {
        return axios.post('/api/user/register', {
            data: params
        })
    },
    // 直接留言
    pushMessage(params) {
        return axios.post('/api/message', {
            ...params
        })
    },
    // 回复留言
    replyMessage(params) {
        return axios.post('/api/message', {
            data: params
        })
    },
    // 分页获取留言
    getMessageByPage(params){
        return axios.get('/api/message',{
            params
        })
    },
    cancleLogin(){
        return axios.post('/api/user/cancle')
    }
}